<template>
  <FButton
    class="effect-selection-button"
    @click="handleButton()"
    :text="effect.text"
  />
</template>

<script>
import { mapActions } from 'vuex'

import FButton from './templates/Fbutton'

export default {
  components: {
    FButton,
  },
  methods: {
    ...mapActions({
      activateCardEffect: 'ACTIVATE_CARD_EFFECT',
    }),
    handleButton() {
      this.activateCardEffect({
        playerIndex: this.playerIndex,
        cardIndex: this.cardIndex,
        effectIndex: this.effectIndex,
      })
    },
  },
  props: {
    cardIndex: Number,
    effect: Object,
    effectIndex: Number,
    playerIndex: Number,
  },
}
</script>
