import { fLog } from './logger'
import { preParser, findParams } from './effectParser'

export function mapArrayWithIndex(array) {
  return array.map((el, index) => {
    el.index = index
    return el
  })
}

export function isActionPossible(players, effect) {
  if (!effect.selectables?.length) {
    fLog.log(`ACTION ${effect.action} IS POSSIBLE`, 'UTIL', 3)
    return true
  }
  let targetFound = []

  selectableLoop: for (const selectable of effect.selectables) {
    for (const [playerIndex, player] of players.entries()) {
      if (canBeSelected('Player', selectable, playerIndex, effect)) {
        if (
          !targetFound.filter(
            (context) => context.length === 1 && context[0] === playerIndex
          ).length
        ) {
          targetFound.push([playerIndex])
          continue selectableLoop
        }
      }
      for (const [cardIndex, card] of player.cards.entries()) {
        if (
          canBeSelected('Card', selectable, playerIndex, effect, card.status) &&
          effectIsApplicable(effect, card)
        ) {
          if (
            !targetFound.filter(
              (context) =>
                context.length === 2 &&
                context[0] === playerIndex &&
                context[1] === cardIndex
            ).length
          ) {
            targetFound.push([playerIndex, cardIndex])
            continue selectableLoop
          }
        }
      }
    }
  }

  if (targetFound.length >= effect.selectables.length) {
    fLog.log(`ACTION ${effect.action} IS POSSIBLE`, 'UTIL', 3)
    return true
  } else {
    fLog.log(`ACTION ${effect.action} IS IMPOSSIBLE`, 'UTIL', 3)
    return false
  }
}

export function searchHooks(type, players, effectObject) {
  const effectTrigger = effectObject.effect
  fLog.log(`SEARCHING FOR ${type} MATCH WITH ${effectTrigger}`, 'HOOK', 3)
  const effectsMatching = []
  for (const [playerIndex, player] of players.entries()) {
    for (const [cardIndex, card] of player.cards.entries()) {
      for (const effObj of card.effects) {
        let effectHook = effObj.text
        const isSpecialEffect = effectHook.startsWith('special')
        if (card.status == 'play' || isSpecialEffect) {
          if (isSpecialEffect) {
            effectHook = effectHook
              .split(' ')
              .slice(1)
              .join(' ')
          }
          if (
            (type == 'before' ? ['before', 'prevent'] : ['after']).includes(
              effectHook.split(' ')[0]
            )
          ) {
            let effectHookCond = effectHook
              .split(':')[0]
              .trim()
              .split(' ')
              .slice(1)
              .join(' ')
            if (effectHookCond.includes(' > ')) {
              const leftHand = effectHookCond.split(' > ')[0]
              const rightHand = effectHookCond.split(' > ')[1]
              if (rightHand == 'this.count(Marker)') {
                if (
                  players[effectObject.context[0]]?.cards[
                    effectObject.context[1]
                  ]?.life
                ) {
                  effectHookCond = `this.remove(Marker, ${players[
                    effectObject.context[0]
                  ].cards[effectObject.context[1]].life - leftHand}+)`
                }
              } else {
                fLog.warn(
                  `THIS COMPARISON HOOK WAS NOT IMPLEMENTED: ${effectHookCond}`,
                  'HOOK'
                )
              }
            }
            if (
              isSameEffect(effectObject, {
                effect: effectHookCond,
                context: [playerIndex, cardIndex],
              })
            ) {
              if (effectHook.startsWith('prevent')) {
                effectsMatching.push({
                  effect: '#CANCEL_NEXT',
                  context: [playerIndex, cardIndex],
                })
              } else {
                effectsMatching.push({
                  effect: effectHook.split(':')[1].trim(),
                  context: [playerIndex, cardIndex],
                })
              }
            }
          }
        }
      }
    }
  }
  if (effectsMatching.length) {
    fLog.log(
      `FOUND MATCHING EFFECTS: ${JSON.stringify(
        effectsMatching.map((e) => e.effect)
      )}`,
      'HOOK',
      3
    )
  } else {
    fLog.log('NO MATCHING EFFECT FOUND', 'HOOK', 4)
  }
  return effectsMatching
}

export function canBeSelected(
  entityType,
  selectable,
  playerIndex,
  currentEffect,
  status
) {
  const correctEntity = !selectable.entity || selectable.entity == entityType
  const correctPlayerIndex =
    selectable.playerIndex == undefined || selectable.playerIndex == playerIndex
  const correctSpecifier =
    !selectable.specifier ||
    selectable.specifier == 'all' ||
    (selectable.specifier == 'opp' &&
      playerIndex !== currentEffect.context[0]) ||
    (selectable.specifier == 'yrs' && playerIndex === currentEffect.context[0])
  const correctCardState = !selectable.state || selectable.state == status
  return (
    correctEntity && correctPlayerIndex && correctSpecifier && correctCardState
  )
}

/**
 * @param Effect effect
 * @param Card card
 * @returns true if the effect can be applied to the card
 */
export function effectIsApplicable(effect, card) {
  return !effect.effectToQueue?.startsWith('this.remove(Marker') || card.life
}

function isSameEffect(effectTrigger, effectHook) {
  const sameLabel = effectTrigger?.label == effectHook.effect
  const sameContext =
    effectTrigger.context &&
    effectTrigger.context[0] == effectHook.context[0] &&
    effectTrigger.context[1] == effectHook.context[1]

  if (!sameContext) {
    return false
  } else if (sameLabel) {
    return true
  }

  const [actionContextTrigger, actionTrigger] = preParser(
    effectTrigger.effect
  ).split('.')
  const [actionHookTrigger, actionHook] = preParser(effectHook.effect).split(
    '.'
  )

  let sameAction =
    actionTrigger &&
    actionHook &&
    actionTrigger.split('(')[0] == actionHook.split('(')[0] &&
    actionContextTrigger == actionHookTrigger

  if (sameAction) {
    const paramsTrigger = findParams(actionTrigger)
    const paramsHook = findParams(actionHook)
    for (const [index, param] of paramsHook.entries()) {
      let matchParams = param == paramsTrigger[index]
      if (param.includes('+')) {
        matchParams = +paramsTrigger[index] >= +param.split('+')[0]
      }
      sameAction = matchParams
    }
  }
  return sameAction
}
