<template>
  <transition name="card-info-smooth">
    <div v-if="showInfo" class="info-overlay" @click="showInfo = false">
      <div class="card-info">
        {{ card.name }}
        <div class="card-info-life">
          <div class="card-info-life-number">
            {{ card.life ? card.life : '' }}
          </div>
          <img
            v-if="card.life"
            class="card-info-heart"
            src="../assets/heart.svg"
          />
        </div>
        <div class="card-info-box">
          <div
            v-html="
              card.effects.length
                ? card.effects
                    .map((effect) => effect.text)
                    .reduce((acc, effect) => acc + '<br />' + effect)
                : 'No effect.'
            "
          />
          <div class="card-footer">
            <Fbutton
              v-if="showButton"
              class="card-button card-info-box-button-invisible"
              @click="handleButton()"
              inner-class="card-button card-info-box-button-visible"
              :text="actionText"
            />
            <div class="card-location-container">
              <img
                class="card-location"
                :src="require(`../assets/${card.status}.png`).default"
              />
              <span class="tooltip-card-location">
                {{ card.status }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
  <transition name="card-info-smooth">
    <div v-if="showEffects" class="info-overlay" @click="showEffects = false">
      <div class="effect-selection">
        <p class="effect-selection-title">
          Chose an effect
        </p>
        <Effect
          v-for="effect in activableEffects"
          :key="effect.index"
          :effect="card.effects[effect.index]"
          :effectIndex="effect.index"
          :playerIndex="playerIndex"
          :cardIndex="cardIndex"
        />
      </div>
    </div>
  </transition>
  <div class="card" :class="{ active: isSelected }">
    <div class="card-click-area" @click="showInfo = true">
      {{ card.name }}
      <div class="card-life">
        {{ card.life ? card.life : '' }}
        <img v-if="card.life" src="../assets/heart.svg" class="card-heart" />
        <br />
      </div>
    </div>
    <Fbutton
      v-if="showButton"
      class="card-button card-button-invisible"
      @click="handleButton()"
      inner-class="card-button"
      :text="actionText"
    />
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

import {
  canBeSelected,
  mapArrayWithIndex,
  effectIsApplicable,
} from '../util/utils'
import { determinesActionText, effectIsActivable } from '../util/cardUtils'
import { fLog } from '../util/logger'

import Effect from './Effect'
import Fbutton from './templates/Fbutton'

export default {
  components: {
    Effect,
    Fbutton,
  },
  data() {
    return {
      showEffects: false,
      showInfo: false,
    }
  },
  computed: {
    ...mapState({
      currentEffect: 'currentEffect',
    }),
    actionText() {
      return determinesActionText(this.currentEffect)
    },
    activableEffects() {
      return mapArrayWithIndex(this.card.effects).filter(effectIsActivable)
    },
    canActivateEffects() {
      return !!(
        this.card.status == 'play' &&
        this.card.effects.filter(effectIsActivable).length
      )
    },
    isActivable() {
      return !!(this.card.status == 'hand' && this.card.effects.length)
    },
    isSelectable() {
      if (!this.currentEffect.selectables?.length || this.isSelected) {
        return false
      }
      if (!effectIsApplicable(this.currentEffect, this.card)) {
        return false
      }
      for (const [
        selectableIndex,
        selectable,
      ] of this.currentEffect.selectables.entries()) {
        if (
          canBeSelected(
            'Card',
            selectable,
            this.playerIndex,
            this.currentEffect,
            this.card.status
          )
        ) {
          return selectableIndex + 1
        }
      }
      return false
    },
    isSelected() {
      if (this.currentEffect?.selected?.length) {
        for (const position of this.currentEffect.selected) {
          if (
            position[0] == this.playerIndex &&
            position[1] == this.cardIndex
          ) {
            return true
          }
        }
      }
      return false
    },
    showButton() {
      return (
        this.isSelectable ||
        (this.playerCanActivate &&
          (this.isActivable || this.canActivateEffects))
      )
    },
  },
  methods: {
    ...mapActions({
      activateCard: 'ACTIVATE_CARD',
      selectCard: 'SELECT_ENTITY',
    }),
    // This dispatch the action that should be made when clicking on the button
    handleButton() {
      if (this.isSelectable) {
        this.selectCard({
          selectionIndex: this.isSelectable - 1,
          position: [this.playerIndex, this.cardIndex],
        })
      } else if (this.isActivable) {
        this.activateCard({
          cardIndex: this.cardIndex,
          playerIndex: this.playerIndex,
        })
      } else if (this.canActivateEffects) {
        this.showEffects = true
      } else {
        fLog.error(
          'BUTTON CLICKED WHEN IT SHOULD BE IMPOSSIBLE',
          `PLAYER:${this.playerIndex}.CARD:${this.cardIndex}`
        )
      }
    },
  },
  props: {
    card: Object,
    cardIndex: Number,
    playerCanActivate: Boolean,
    playerIndex: Number,
  },
}
</script>
