<template>
  <div class="player" :class="{ 'active-player': active }">
    {{ player.name }}
    <Fbutton
      v-if="isSelectable"
      class="player-button-invisible"
      @click="selectPlayer()"
      inner-class="player-button"
      text="Select"
    />
    <div class="container life-container" :class="{ active }">
      Life remaining:
      <img
        v-for="index in player.life > 0 ? player.life : 0"
        :key="index"
        class="heart"
        src="../assets/heart.svg"
      />
      <span class="tooltip-life">
        {{ player.life }}
      </span>
    </div>
    <div
      v-for="type in ['hand', 'play', 'deck', 'discard']"
      :key="type"
      class="container"
      :class="{ active }"
    >
      <div class="container-name">
        {{ type.toUpperCase() }}
      </div>
      <Card
        v-for="card in cardsWithStatus(type)"
        :key="card.index"
        :playerCanActivate="playerCanActivate"
        :card="player.cards[card.index]"
        :cardIndex="card.index"
        :playerIndex="playerIndex"
      />
    </div>
    <Fbutton
      v-if="playerCanActivate"
      class="player-button-invisible"
      @click="endTurn()"
      inner-class="player-button"
      text="END TURN"
    />
  </div>
</template>

<script>
import Card from './Card'
import Fbutton from './templates/Fbutton'

import { canBeSelected, mapArrayWithIndex } from '../util/utils'

import { mapActions, mapState } from 'vuex'

export default {
  components: {
    Card,
    Fbutton,
  },
  computed: {
    ...mapState({
      currentEffect: 'currentEffect',
    }),
    isSelectable() {
      if (!this.isSelected && this.currentEffect.selectables?.length) {
        for (const [
          selectableIndex,
          selectable,
        ] of this.currentEffect.selectables.entries()) {
          if (
            canBeSelected(
              'Player',
              selectable,
              this.playerIndex,
              this.currentEffect
            )
          ) {
            return selectableIndex + 1
          }
        }
      }
      return false
    },
    isSelected() {
      if (this.currentEffect.selected?.length) {
        for (const position of this.currentEffect.selected) {
          if (position[0] == this.playerIndex && !position[1]) {
            return true
          }
        }
      }
      return false
    },
    playerCanActivate() {
      return this.active && !this.currentEffect.action
    },
  },
  methods: {
    ...mapActions({
      endTurn: 'END_TURN',
      selectEntity: 'SELECT_ENTITY',
    }),
    cardsWithStatus(status) {
      return mapArrayWithIndex(this.player.cards).filter(
        (card) => card.status == status
      )
    },
    selectPlayer() {
      this.selectEntity({
        selectionIndex: this.isSelectable - 1,
        position: [this.playerIndex],
      })
    },
  },
  watch: {
    'player.life'(life) {
      if (life <= 0 && this.active) {
        this.endTurn()
      }
    },
  },
  props: {
    active: Boolean,
    player: Object,
    playerIndex: Number,
  },
}
</script>
