import { fLog } from '../util/logger'

//TODO: REFACTOR THIS FUNCTION
// Converts an effect string into an effect object
export function effectParser(effectObject, players) {
  fLog.log(`PARSING EFFECT: ${effectObject.effect}`, 'PARSER', 2)
  const effect = preParser(effectObject.effect)
  const context = effectObject.context

  let selectables = []
  const selected = []

  if (effect.startsWith('#')) {
    return { action: effect, selectables, context }
  }

  const effectSplit = effect.split('.')

  const effectChainLength = effectSplit.length

  if (effectChainLength < 2) {
    fLog.log(`ACTION FOUND BEFORE PARSING: ${effectSplit[0]}`, 'PARSER', 4)
    return { action: effectSplit[0], selectables, context }
  }

  // Moves downward through the target chain
  for (let i = effectChainLength - 2; i >= 0; i--) {
    const targetFunction = effectSplit[i].split('(')[0]
    const targetParams = findParams(effectSplit[i])
    const forbiddenPlayerIndexes = []
    if (targetParams.includes('opp')) {
      forbiddenPlayerIndexes.push(context[0])
    }
    if (targetFunction == 'any') {
      const tempTarget = {}
      let nextParam = 0
      const params = findParams(effectSplit[i])
      // Check for entity
      if (entities.includes(params[nextParam])) {
        tempTarget.entity = params[nextParam]
        nextParam++
      }
      // Check for specifier
      if (specifiers.includes(params[nextParam])) {
        tempTarget.specifier = params[nextParam]
        nextParam++
      }
      // Check for state
      if (states.includes(params[nextParam])) {
        tempTarget.state = params[nextParam].split('"')[1]
        nextParam++
      }
      // Check for count
      if (+params[nextParam]) {
        selectables.length = params[nextParam]
        selectables.fill(tempTarget)
      } else {
        selectables.length = 1
        selectables.fill(tempTarget)
      }
    } else if (targetFunction == 'Players') {
      if (selectables.length) {
        const newSelectables = []
        for (const playerIndex in players) {
          if (!forbiddenPlayerIndexes.includes(+playerIndex)) {
            selectables.map((selectable) => {
              newSelectables.push({
                ...selectable,
                playerIndex,
              })
            })
          }
        }
        selectables = newSelectables
      } else {
        for (const playerIndex in players) {
          if (!forbiddenPlayerIndexes.includes(+playerIndex)) {
            selected.push([+playerIndex])
          }
        }
      }
    } else if (targetFunction == 'this') {
      selected.push(context)
    } else if (targetFunction == 'you') {
      if (selectables.length) {
        for (const selectable of selectables) {
          selectable.playerIndex = context[0]
        }
      } else {
        selected.push([context[0]])
      }
    }
  }

  let action
  let payload = []
  let effectToQueue

  const effectAction = effectSplit[effectChainLength - 1]
  if (selectables.length) {
    action = 'SELECTION'
    effectToQueue = `this.${effectAction}`
  } else {
    action = actionsParsed[effectAction.split('(')[0]]

    payload = findParams(effectAction)

    if (!action) {
      fLog.warn(`ACTION FOR EFFECT ${effect} UNKNOWN`, 'PARSER')
    }
  }

  fLog.log(`PARSED ACTION: ${action} ${payload}`, 'PARSER', 3)

  return {
    action,
    effectToQueue,
    selectables,
    selected,
    payload,
    context,
  }
}

export function isQueueable(effect) {
  return !['after', 'before', 'prevent', 'special'].includes(
    effect.split(' ')[0]
  )
}

export function findParams(string) {
  const regexParams = /\((.+)\)/

  const rawParams = string.match(regexParams)
  return rawParams ? rawParams[1].split(', ') : []
}

// TODO: PRE-PARSING SHOULD OCCUR AT DATA READ TIME
export function preParser(originalEffect) {
  let effect = originalEffect
  let previousEffect

  do {
    previousEffect = effect

    /*
    // Replace the pool's effects
    effect = effect.replace(/<(\w+)>/g, (_, name) => {
      return effectPool[name]
    })
    */

    // Replace the 'draw' function
    effect = effect.replace(/\.draw\((\d+)?\)/g, (_, count) => {
      return `.any(Card, "deck", ${count}).hand()`
    })

    effect = effect.replace(/\.deck\(\)/g, '.change("deck")')
    effect = effect.replace(/\.discard\(\)/g, '.change("discard")')
    effect = effect.replace(/\.hand\(\)/g, '.change("hand")')
    effect = effect.replace(/\.play\(\)/g, '.change("play")')
  } while (effect != previousEffect)

  return effect
}

const actionsParsed = {
  change: 'CHANGE_CARD_STATUS',
  draw: 'DRAW',
  discard: 'DISCARD',
  give: 'GIVE_ENTITY',
  play: 'PLAY',
  remove: 'REMOVE_ENTITY',
}

// const contextualizers = ['here', 'it', 'now', 'this', 'you']
const entities = ['Card', 'Effect', 'Marker', 'Player', 'Turn']
const specifiers = ['all', 'opp', 'yrs']
/*const globalEntities = [
  'All',
  'Cards',
  'Effects',
  'Markers',
  'Players',
  'Turns',
]*/
const states = ['"deck"', '"discard"', '"hand"', '"play"']
