<template>
  <div class="game">
    <Player
      v-for="player in activePlayers"
      :key="player.index"
      :active="turn % players.length == player.index"
      :player="player"
      :playerIndex="player.index"
    />
    <div class="turn-number">Turn: {{ turn + 1 || 0 }}</div>
    <div class="current-action">
      Current action:
      {{
        effectsToResolve.length
          ? effectsToResolve[0].effect
          : 'Activate effects from your hand or end your turn'
      }}
    </div>
    <div v-if="endGame" class="info-overlay">
      <div class="end-game">
        <img
          class="end-game-image"
          :src="
            activePlayers.length
              ? require('../assets/end-game-victory.png').default
              : require('../assets/end-game-draw.png').default
          "
        />
        <br />
        {{
          activePlayers.length
            ? `${activePlayers[0].name} wins!`
            : 'All players lost at the same time'
        }}
      </div>
    </div>
  </div>
</template>

<script>
// TODO: ADD ERROR CHECK FOR ALL FILES
// TODO: FIX CALL OF AFTER HOOKS
// TODO: FIX COHERENCE STATE/STATUS
// TODO: FIX END GAME WHEN CURRENT PLAYER LOSES
import Player from './Player'

import { mapState, mapActions } from 'vuex'

import { fLog } from '../util/logger'
import { mapArrayWithIndex } from '../util/utils'

export default {
  components: {
    Player,
  },
  computed: {
    ...mapState({
      effectsToResolve: 'effectsToResolve',
      selectables: (state) => state.currentEffect.selectables,
      players: 'players',
      turn: 'turn',
    }),
    activePlayers() {
      return mapArrayWithIndex(this.players).filter((player) => player.life > 0)
    },
    endGame() {
      return this.activePlayers.length < 2 && !this.effectsToResolve.length
    },
  },
  methods: mapActions({
    activateEffect: 'ACTIVATE_EFFECT',
    initGame: 'INIT_GAME',
    workEffect: 'WORK_EFFECT',
  }),
  mounted() {
    this.initGame()
  },
  watch: {
    /**
     * When the current action should activate because its target is defined,
     * this watcher is responsible for the activation of the action.
     */
    selectables: {
      handler(selectables) {
        if (selectables && !selectables.length) {
          this.activateEffect()
        }
      },
      deep: true,
    },
    /**
     * When an effect is queued,
     * this watcher is responsible for alerting the store.
     */
    effectsToResolve: {
      handler(effects) {
        if (effects?.length > 0) {
          fLog.log(
            `EFFECTS SHOULD ACTIVATE: ${effects.map((e) => e.effect)}`,
            'GAME',
            1
          )
          this.workEffect()
        }
      },
      deep: true,
    },
  },
}
</script>
