import { isQueueable } from './effectParser'

/**
 * @param Effect effect
 * @returns the text for the button depending on the effect
 */
export function determinesActionText(effect) {
  if (!effect.action) {
    return 'Activate'
  }
  return actionsForCard[effect.action]
    ? actionsForCard[effect.action][effect.payload] || 'Select'
    : 'Select'
}

/**
 * @param Effect effect
 * @returns true if the effect can be activated by the player
 */
export function effectIsActivable(effect) {
  return isQueueable(effect.text) && effect.status == 'ready'
}

/**
 * Translates actions to text for card buttons
 */
const actionsForCard = {
  CHANGE_CARD_STATUS: {
    '"discard"': 'Discard',
    '"hand"': 'Draw',
  },
}
