import { effectParser, isQueueable } from '../util/effectParser'
import { isActionPossible, searchHooks } from '../util/utils'
import { fLog } from '../util/logger'

export const gameMutations = {
  ADD_EFFECTS_AT_INDEX(state, { index, effects }) {
    state.effectsToResolve.splice(index, 0, ...effects)
  },

  // End the turn and ready all effects
  END_TURN(state) {
    for (const player of state.players) {
      for (const card of player.cards) {
        for (const effect of card.effects) {
          effect.status = 'ready'
        }
      }
    }
    state.turn++
  },

  QUEUE_EFFECT(state, { effect, context, label }) {
    state.effectsToResolve.push({ effect, context, label, hooks: false })
  },

  // Removes an effect after it has been resolved
  RESOLVE_EFFECT(state) {
    fLog.log(`RESOLVING EFFECT: ${state.effectsToResolve[0].effect}`, 'TECH', 2)
    state.effectsToResolve.splice(0, 1)
    state.currentEffect = state.effectsToResolve[0] || {}
  },

  SEARCHED_HOOKS(state) {
    state.effectsToResolve[0].hooks = true
  },

  SELECT_ENTITY(state, { selectionIndex, position }) {
    state.currentEffect.selectables.splice(selectionIndex, 1)
    state.currentEffect.selected.push(position)
  },

  SET_CURRENT_EFFECT(state, effect) {
    fLog.log(`SETTING ACTION: ${effect.action} ${effect.payload}`, 'TECH', 2)
    state.currentEffect = effect
  },

  SETTLE_EFFECT(state, { playerIndex, cardIndex, effectIndex }) {
    fLog.log(
      `SETTLE EFFECT AT: PLAYER:${playerIndex}.CARD:${cardIndex}.EFFECT:${effectIndex}`,
      'TECH',
      3
    )
    state.players[playerIndex].cards[cardIndex].effects[effectIndex].status =
      'done'
  },

  START_TURN(state) {
    state.effectsToResolve.push({
      effect: 'you.draw()',
      context: [state.turn % state.players.length],
    })
  },
}

export const gameActions = {
  ACTIVATE_CARD({ state, commit }, { playerIndex, cardIndex }) {
    commit('QUEUE_EFFECT', {
      effect: 'this.play()',
      context: [playerIndex, cardIndex],
    })
    for (const effObj of state.players[playerIndex].cards[cardIndex].effects) {
      const effect = effObj.text
      if (isQueueable(effect)) {
        fLog.log(
          `QUEUEING EFFECT: ${effect} IN PLAYER:${playerIndex}.CARD:${cardIndex}`,
          'TECH',
          2
        )
        commit('QUEUE_EFFECT', {
          effect,
          context: [playerIndex, cardIndex],
          label: '@ACTIVATION',
        })
      }
    }
    commit('QUEUE_EFFECT', {
      effect: 'this.discard()',
      context: [playerIndex, cardIndex],
      label: '@DISCARD',
    })
  },

  ACTIVATE_CARD_EFFECT(
    { state, commit },
    { playerIndex, cardIndex, effectIndex }
  ) {
    commit('SETTLE_EFFECT', { playerIndex, cardIndex, effectIndex })
    commit('QUEUE_EFFECT', {
      effect:
        state.players[playerIndex].cards[cardIndex].effects[effectIndex].text,
      context: [playerIndex, cardIndex],
    })
  },

  ACTIVATE_EFFECT({ state, commit, dispatch }) {
    if (state.currentEffect.action.startsWith('#')) {
      dispatch(state.currentEffect.action.slice(1))
    } else if (state.currentEffect.action == 'SELECTION') {
      for (const target of state.currentEffect.selected) {
        commit('ADD_EFFECTS_AT_INDEX', {
          index: 1,
          effects: [
            {
              effect: state.currentEffect.effectToQueue,
              context: target,
            },
          ],
        })
      }
    } else {
      for (const target of state.currentEffect.selected) {
        dispatch(state.currentEffect.action, {
          target,
          payload: state.currentEffect.payload,
        })
      }
    }
    commit('RESOLVE_EFFECT')
  },

  END_TURN({ state, commit, dispatch }) {
    commit('END_TURN')
    if (state.players[state.turn % state.players.length].life <= 0) {
      dispatch('END_TURN')
    } else {
      commit('START_TURN')
    }
  },

  INIT_GAME({ commit }) {
    commit('QUEUE_EFFECT', {
      effect: 'Players.draw(3)',
    })
    commit('QUEUE_EFFECT', { effect: '#FIRST_TURN' })
  },

  SELECT_ENTITY({ commit }, { selectionIndex, position }) {
    commit('SELECT_ENTITY', { selectionIndex, position })
  },

  WORK_EFFECT({ state, commit }) {
    // Search for hooks to activate before the next effect
    if (!state.effectsToResolve[0].hooks) {
      commit('SEARCHED_HOOKS')
      const beforeHooks = searchHooks(
        'before',
        state.players,
        state.effectsToResolve[0]
      )
      if (beforeHooks.length) {
        return commit('ADD_EFFECTS_AT_INDEX', {
          index: 0,
          effects: beforeHooks,
        })
      } else {
        // Search for hooks to activate after the resolved effect
        const afterHooks = searchHooks(
          'after',
          state.players,
          state.effectsToResolve[0]
        )
        if (afterHooks.length) {
          commit('ADD_EFFECTS_AT_INDEX', { index: 1, effects: afterHooks })
        }
      }
    }

    const effect = effectParser(state.effectsToResolve[0], state.players)

    if (isActionPossible(state.players, effect)) {
      commit('SET_CURRENT_EFFECT', effect)
    } else {
      commit('RESOLVE_EFFECT')
    }
  },
}
