export function dataParser(data) {
  data.map((player) => {
    player.name = player.name || 'Player'
    player.life = 10
    player.cards = player.cards || []
    player.cards.map((card) => {
      card.name = card.name || 'Card'
      card.status = 'deck'
      card.life = 0
      card.effects = card.effects || []
      card.effects = card.effects.map((effect) => {
        return (effect = { text: effect, status: 'ready' })
      })
    })
  })
  return data
}
