export const cardMutations = {
  CANCEL_NEXT(state) {
    for (const [index, effect] of state.effectsToResolve.entries()) {
      if (index > 0 && effect.hooks) {
        state.effectsToResolve.splice(index, 1)
        break
      }
    }
  },

  // Change a card status from its index
  CHANGE_CARD_STATUS(state, { target, status }) {
    state.players[target[0]].cards[target[1]].status = status
  },

  // Change a player or a card's life
  CHANGE_LIFE(state, { playerIndex, cardIndex, quantity }) {
    if (cardIndex) {
      state.players[playerIndex].cards[cardIndex].life += quantity
    } else {
      state.players[playerIndex].life += quantity
    }
  },

  // Starts the turn count
  FIRST_TURN(state) {
    state.turn = 0
  },
}

export const cardActions = {
  CANCEL_NEXT({ commit }) {
    commit('CANCEL_NEXT')
  },

  CHANGE_CARD_STATUS({ commit }, { target, payload }) {
    commit('CHANGE_CARD_STATUS', { target, status: payload[0].split('"')[1] })
  },

  FIRST_TURN({ commit }) {
    commit('FIRST_TURN')
  },

  GIVE_ENTITY({ commit }, { target, payload }) {
    commit('CHANGE_LIFE', {
      playerIndex: target[0],
      cardIndex: target[1],
      quantity: +payload[1],
    })
  },

  REMOVE_ENTITY({ commit }, { target, payload }) {
    commit('CHANGE_LIFE', {
      playerIndex: target[0],
      cardIndex: target[1],
      quantity: -payload[1],
    })
  },
}
