import { createStore } from 'vuex'

import data from './data.json'

import { dataParser } from '../util/dataParser'
import { gameMutations, gameActions } from './mechanics'
import { cardMutations, cardActions } from './cardEffects'

export const store = createStore({
  state: {
    currentEffect: {
      action: undefined,
      effectToQueue: '',
      payload: [],
      selectables: [],
      selected: [],
    },
    effectsToResolve: [],
    players: dataParser(data),
    turn: undefined,
  },
  mutations: {
    ...gameMutations,
    ...cardMutations,
  },
  actions: {
    ...gameActions,
    ...cardActions,
  },
})
