const logLevel = 5

export const fLog = {
  error: (message, trace) => Logger('error', message, trace),
  warn: (message, trace) => Logger('warn', message, trace),
  log: (message, trace, level) => Logger('log', message, trace, level),
}

function Logger(type, message, trace, level) {
  if (!level || logLevel >= level) {
    console[type](trace ? `[${trace}] ${message}` : message)
  }
}
